<template>
  <div class="home">
      <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" @click="tostore">
                    <div class="con">
                        <div class="userImg"><img src="../../assets/logo.png"></div>
                        <div class="text">
                            <div class='name'></div>
                            <div>My English name is WHowe</div>
                            <div></div>
                            <h4>86184893@qq.com</h4>
                        </div>
                    </div>
                    <div class="botline"></div>
                   
                </div>
                <div class="swiper-slide">
                    <MrTabBar></MrTabBar>
                </div>
                <div class="swiper-slide"><MrRipple></MrRipple></div>
                <div class="swiper-slide" @click="topersonal"></div>
                <div class="swiper-slide" @click="tocart"></div>
                <div class="swiper-slide"><MrWorks></MrWorks></div>
                <div class="swiper-slide" @click="towaterfall">
                    <div class="waterfall">
                         <h3></h3>
                        <div class="pic">
                         <img src="@/assets/images/waterfall/works.png">
                        </div>
                       
                    <div>
                        <h5>.
                        ,css</h5>
****
     <p>1. </p>
     <p>2. </p>
     <p>3. top</p>
     <p>4. </p>
     <p>5. 1-4</p>
     <p>6. </p>
     </div>
                    </div>
                </div>
            </div>
            <!--  -->
            <!-- <div class="swiper-pagination"></div> -->
            
            <!--  -->
            <!-- <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div> -->
            
            <!--  -->
            <!-- <div class="swiper-scrollbar"></div> -->
        </div>
  </div>
</template>


<script>
import MrTabBar from '@/components/skillcomponent';
import MrRipple from '@/components/ripple';
import MrWorks from '@/components/works';

import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
export default {
     components:{
            MrTabBar,
            MrRipple,
            MrWorks
        },
  mounted(){
    var mySwiper = new Swiper('.swiper-container', {
        pagination: {
            el: '.swiper-pagination',
        },
        slidesPerView :'auto',
        autoplay:5000,
        direction:'vertical',
        grabCursor:true,
        autoplayDisableOnInteraction:false,
        mousewheelControl:true,
        autoHeight:true,
        speed:500
    })
  },
  methods:{
     tostore(){
         this.$router.push('store')
     },
     topersonal(){
         this.$router.push('personal')
     },
     tocart(){
         this.$router.push('cart')
     },
     towaterfall(){
         this.$router.push('waterfall')
     }
  }
}
</script>
<style lang="scss" scoped>
.waterfall{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width: 90%;
    h3{
        text-align: center;
    }
    p{
        margin-bottom: .1rem;
    }
    .pic{
        width: 3rem;
        height: 4rem;
        margin: auto;
        text-align: center;
        img{
            widows:100%;
            height:100%;
            object-fit: contain;
        }
    }
}
.swiper-container{
    width: 100vw;
    height: 100vh;
    position:relative;
    .swiper-slide{
        height: 100vh;
        margin:auto;
        overflow-y: auto;
        overflow-x: hidden;
        .con{
            position:absolute;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);
            text-align:center;
            //
            background-image: linear-gradient(135deg, deeppink, dodgerblue);
            background-clip:text;
            -webkit-background-clip:text;
            box-decoration-break: clone; 
            -webkit-box-decoration-break: clone; 
            color: transparent;
            .text{
                line-height: .6rem;
         
            }
           h4{
            display: block;
            width: 600px;
            height: 200px;
            /* 0~100%*/   /*linear-gradient()*/
            background-image: -webkit-linear-gradient(left, #3498db, #f47920 10%, #d71345 20%, #f7acbc 30%, #ffd400 40%, #3498db 50%, #f47920 60%, #d71345 70%, #f7acbc 80%, #ffd400 90%, #3498db);
            color: transparent; /**/
            -webkit-text-fill-color: transparent;
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-background-size: 200% 100%; /*background-position*/
            background-size: 200% 100%;
            /*  */
            animation: masked-animation 4s infinite linear;
            -webkit-animation: masked-animation 4s infinite linear;
            }
            @keyframes masked-animation {
                0% {
                    background-position: 0 0;   /*background-position */
                }
                100% {
                    background-position: -100% 0;
                }
            }

            @-webkit-keyframes masked-animation {
                0% {
                    background-position: 0 0;
                }
                100% {
                    background-position: -100% 0;
                }
            }
            .userImg{
                width:80px;
                height:80px;
                margin:20px auto;
                img{
                    border-radius:100%;
                    width:100%;
                    height:100%;
                    object-fit:cover;
                    background:#f7f7f7;
                }
            }
        }
        .botline{
            position:absolute;
            bottom:20px;
            left:50%;
            transform:translateX(-50%);
            height:30px;
            font-size:30px;
            animation:myline .8s infinite;
        }
    }
    
}
@keyframes myline
{
from {opacity:1;}
to {opacity:0;}
}
</style>




